    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-top: 25px;
    align-items: center;
}

.dish-image {
    width: 300px;
    height: 200px;
    grid-area: image;
    justify-self: stretch;
    justify-self: center;
    border-radius: 10px;
}

